@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}



#root {
  padding-top: 30px;
}

.App {
  display: flex;
  justify-content: center;
  background-color: #e0e5ec;
  width: 45%;
  margin-right: auto;
  margin-left: auto;
  padding: 30px;
  border-radius: 5px;
}

.PostsPage {
  width: 100%;
}

.Post {
  display: flex;
  flex-direction: column;
  list-style: none;
  border: 1px solid #d9dce0;
  background-color: white;
  margin-top: 20px;
  padding: 20px;
}

.CreatePostBlock {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  background-color: white;
  border: 1px solid #d9dce0;
  border-radius: 5px;
  padding: 10px 20px;
}

.createPostButton {
  text-decoration: none;
  background-color: #4469ae;
  border-radius: 3px;
  color: white;
  padding: 3px 8px;
}

.UserInfoBlock {
  display: flex;
  align-items: center;
}

.UserImage {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.PostInfoBlock {
  margin-left: 20px;
}

.UserName {
  color: #4469ae;
  font-weight: bold;
  font-size: 1.1rem;
}

.created {
  color: #6b7378;
  font-weight: lighter;
  margin-top: 10px;
}

.TextPost {
  color: #2a262d;
  font-size: 1.6rem;
}

.TextPostBlock {
  text-decoration: none;
  margin-top: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.PostCreatePage {
    position: relative;
    width: 100%;
    text-align: right;
}

.CreatePostTextArea {
    width: 100%;
    resize: none;
    border: none;
    outline: none;
    padding: 20px;
    color: #6b7378;
    font-size: 1rem;
}

.CreatePostButton {
    margin-top: 15px;
    text-decoration: none;
    background-color: #4469ae;
    border-radius: 3px;
    color: white;
    padding: 3px 8px;
    border: none;
    cursor: pointer;
}

.LeaveCreatePageButton {
    position: absolute;
    top: -30px;
    right: 0;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    background-color: #e0e5ec;
}

.PostPage {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.PostButtonsBlock {
    display: flex;
    justify-content: flex-end;
    border: 1px solid #dcdee1;
    margin-top: 20px;
    padding: 15px;
}

.PostChange {
    text-decoration: none;
    background-color: #4469ae;
    border-radius: 3px;
    color: white;
    font-size: 0.8rem;
    margin-right: 15px;
    padding: 3px 8px;
}

.PostDelete {
    border: none;
    background-color: #b04546;
    border-radius: 3px;
    color: white;
    font-size: 0.8rem;
    padding: 3px 8px;
    cursor: pointer;
}

.EditPostPage {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
}

.HeaderBlock {
    align-self: flex-start;
}

.EditTextArea {
    width: 100%;
    resize: none;
    border: none;
    outline: none;
    padding: 20px;
    margin-top: 20px;
    color: #6b7378;
    font-size: 1rem;
}

.PostChangeButton {
    margin-top: 15px;
    text-decoration: none;
    background-color: #4469ae;
    border-radius: 3px;
    color: white;
    padding: 3px 8px;
    border: none;
    cursor: pointer;
}

.LeaveEditPageButton {
    text-decoration: none;
    color: inherit;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    background-color: #e0e5ec;
}

.ErrorPage {
    display: flex;
}

